ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

li {
  float: right;
  margin:0;
}

li a {
  display: block;
  color: white;
  text-align: center;
  margin: 0px 64px 0px 64px;
  text-decoration: none;
  padding:40px 0px ;
  font-size: large;
}

li a:hover {
  color: #f76236;
}

.frontcontent{
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: block;
  color: white;
  padding-top: 150px;
  text-align: center;
  position: relative;
  padding-bottom: 150px;
}

h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 5.25em;
  line-height: 1.05;
  font-weight: 400;
  letter-spacing: 0.03em;
}

h2{
  font-size: 1.25em;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}

p{
  color: white;
  padding-left: 30%;
  padding-right: 30%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}

.ringtext{
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mainpage{
  background-color: #181818;
  background-image: url(https://uploads-ssl.webflow.com/61d4b50a3b130a78a63644fe/61e57cced52b4c5f7554323e_Header%20Background.png);
}

.contact{
  letter-spacing: 0.1em;
  color:#f76236;
  padding: 0.78em 2.85em;
}

.aboutme{
  background-color:#f76236;
  height: 225px;
  overflow: hidden;
}

.projectimg{
  display: inline-block;
  padding: 50px 100px;
}

.projectimg2{
  width: 600px;
  height: 400px;
}
